blockquote {
    border-left: 0.15rem solid #0171a5;
    padding-left: 1rem;
    font-size: 0.85rem;
    line-height: 1.7;
    font-weight: 500;
}

blockquote.border-0 {
    padding-left: 0;
}

blockquote.icon {
    position: relative;
    border: 0;
    padding: 0;
}

blockquote.icon:before {
    content: "\201d";
    position: absolute;
    top: -1.5rem;
    left: -0.9rem;
    color: rgba(52, 63, 82, 0.05);
    font-size: 10rem;
    line-height: 1;
    z-index: 1;
}

blockquote.icon-top {
    padding-top: 3.75rem;
    position: relative;
}

blockquote.icon-top:before {
    content: "\201c";
    color: #aab0bc;
    opacity: 0.3;
    font-size: 6.5rem;
    font-weight: normal;
    top: 0;
    left: -0.3rem;
}

blockquote.icon-top.text-white:before {
    color: #fff;
}

blockquote.text-center.icon-top:before {
    left: 50%;
    transform: translateX(-52%);
}

.blockquote-footer {
    font-weight: 700;
    margin-top: 0;
    text-transform: uppercase;
    letter-spacing: 0.02rem;
}

.blockquote-details {
    display: flex;
    align-items: center;
    text-align: left;
}

.blockquote-details p {
    font-size: 0.8rem;
}

.blockquote-details .info {
    padding-left: 1rem;
}

.blockquote-details .icon-img img {
    width: 3.5rem !important;
}

.dropcap {
    display: block;
    float: left;
    font-size: calc(1.375rem + 1.5vw);
    line-height: 1;
