html,
body {
    height: 100%;
}

body {
    display: flex;
    flex-direction: column;
}

.content-wrapper,
footer {
    flex-shrink: 0;
}

.content-wrapper {
    flex-grow: 1;
}

.overlay {
    position: relative;
    text-align: center;
    overflow: hidden;
    transform: translate3d(0, 0, 0);
}

figure.overlay img {
    opacity: 1;
    width: 100%;
    max-width: 100%;
    vertical-align: top;
    position: relative;
}

.overlay figcaption,
.overlay .figcaption {
    padding: 0.5rem;
    z-index: 5;
    pointer-events: none;
}

.overlay * {
    transition: all 0.4s ease;
}

@media (prefers-reduced-motion: reduce) {
    .overlay * {
        transition: none;
    }
}

.overlay img {
    transition: none;
}

.overlay:not(.caption-overlay) i {
    display: block;
    font-size: calc(1.265rem + 0.18vw);
}

@media (min-width: 1200px) {
    .overlay:not(.caption-overlay) i {
        font-size: 1.4rem;
    }
}

.overlay a,
.overlay span.bg {
    position: relative;
    display: block;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 3;
    cursor: pointer !important;
}

.overlay span.bg {
    opacity: 0;
    z-index: 4;
    position: absolute;
    width: 100%;
    height: 100%;
}

.overlay span.bg {
    background: rgba(30, 34, 40, 0.6);
}

.overlay.color span.bg {
    background: rgba(63, 120, 224, 0.7);
}

.overlay.light span.bg {
    background: rgba(255, 255, 255, 0.6);
}

.overlay:hover span.bg {
    opacity: 1;
    transition: opacity 0.35s ease-in-out;
}

@media (prefers-reduced-motion: reduce) {
    .overlay:hover span.bg {
        transition: none;
    }
}

.overlay * {
    color: #fff !important;
}

.overlay.light * {
    color: #343f52 !important;
}

.overlay img {
    max-width: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    vertical-align: top;
}

.overlay1 * {
    box-sizing: border-box;
}

.overlay1 figcaption {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 0.75rem 1rem;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    opacity: 0;
    text-align: center;
}

.overlay1 .from-top {
    position: absolute;
    padding: 0.75rem 1rem;
    top: 50%;
    left: 0;
    width: 100%;
    transform: translateY(-80%);
}

.overlay1:hover figcaption,
.overlay1:hover .figcaption {
    opacity: 1;
}

.overlay1:hover figcaption *,
.overlay1:hover .figcaption * {
    transform: translateY(-50%);
}

.overlay2 * {
    position: absolute;
    left: 0;
    margin: 0;
    width: 100%;
    text-align: center;
}

.overlay2>* {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    position: absolute;
}

.overlay2 figcaption {
    height: 100%;
    opacity: 0;
}

.overlay2 img {
    position: relative;
}

.overlay2 .from-top {
    bottom: 50%;
    padding: 0 1rem;
    transform: translate3d(0%, -100%, 0);
}

.overlay2 .from-bottom {
    top: 50%;
    padding: 0 1rem;
    transform: translate3d(0%, 100%, 0);
}

.overlay2:hover * {
    opacity: 1;
}

.overlay2:hover figcaption * {
    transform: translate3d(0%, 0%, 0);
}

.hover-scale {
    overflow: hidden;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    transform: translateY(0);
}

.hover-scale img {
    transition: all 0.35s ease-in-out;
}

.hover-scale:hover img {
    transform: scale(1.05);
}

