}

.pricing-switchers {
    width: 2rem;
    height: 1rem;
    clear: both;
    text-align: center;
    position: relative;
    background-color: rgba(30, 34, 40, 0.07);
    border-radius: 1.5rem;
    border: 0.2rem solid transparent;
    box-sizing: content-box;
}

.pricing-switchers .pricing-switcher {
    cursor: pointer;
    width: 100%;
    float: left;
    height: 1rem;
    line-height: 1rem;
    position: relative;
    z-index: 888;
    transition: 0.3s ease-in-out;
    text-transform: uppercase;
}

