    left: 0;
}

.pricing-switchers .pricing-switcher:nth-child(2).pricing-switcher-active~.switcher-button {
    left: 50%;
}

.pricing-switchers .switcher-button {
    height: 1rem;
    width: 1rem;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 555;
    margin: 0;
    border: none;
    transition: 0.3s ease-in-out;
    border-radius: 100%;
}

