/* Style the tab */
.tab {
    float: left;
    width: 30%;
    height: 300px;
}
.tablinks{
    height: 60px;
}

/* Style the buttons inside the tab */
.tab button {
    display: block;
background-color: #f2f8fb;
color: black;
padding: 5px 45px;
width: 90%;
border: none;
outline: none;
text-align: left;
cursor: pointer;
transition: 0.3s;
font-size: 16px;
margin-bottom: 10px;
border-radius: 7px;
font-weight: bold;
line-height: 1.5;
}

/* Change background color of buttons on hover */
.tab button:hover {
    background-color: #0171a5;
    color: white;
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 10px;
    border-radius: 7px;
}

/* Create an active/current "tab button" class */
.tab button.active {
    background-color: #0171a5;
    color: white;
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 10px;
    border-radius: 7px;
}

/* Style the tab content */


.tabcontent {
    float: left;
    padding: 0px 12px;
    width: 70%;
    border-left: none;
    height: 410px;
}




